@import url(https://fonts.googleapis.com/css?family=Nunito|PT+Serif:400,400i&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}main div.portfolio__items>span{font-size:22px;font-weight:100;font-family:"Nunito"}main div.portfolio__menu>span,.education>span,.about>span{font-size:13px}header div{font-size:40px}header{margin-bottom:150px}main{margin-bottom:80px}main div{padding:20px 0 15px 0;display:grid;grid-template-columns:1fr 4fr 6.5fr}main div.portfolio__items{border-top:1px solid #444;cursor:pointer}main div.portfolio__items:hover{font-style:italic}.education{margin-bottom:75px}.education,.about{font-size:30px;line-height:1.6;font-weight:100}.about>a{color:#444}body{color:#444;padding:40px}

